import React from "react";
import PropTypes from "prop-types";
import styled from "astroturf";

const Container = styled("div", null, "Container", require("./Avatar-Container.module.scss"), "container", "container");

const Img = styled("img", null, "Img", require("./Avatar-Img.module.scss"), "img", "img");

export const Avatar = ({ source, className, alt }) => {
  return (
    <Container className={className}>
      <Img src={source} alt={alt} />
    </Container>
  );
};

Avatar.propTypes = {
  alt: PropTypes.string,
  source: PropTypes.string,
  className: PropTypes.string,
};
